
.testInput{
    width: 90%;
    position: absolute;
    bottom: 80px;
    left: 5%;
  }
  .testInput2{
    bottom: 200px;
  }
  .testInput3{
    bottom: 0px;
  }
  input{
    height: 30px;
    width: 100%;
    padding: 5px 10px;
    box-sizing: border-box;
    box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.8) inset;
    outline: none;
    border: none;
    border-radius: 5px;
  }
  .positionTest{
    position: absolute;
    bottom: 0;
    transition: 1000ms;
    width: 100%;
    height: 40px;
    background-color: orange;
  }
  .positionTestTop{
    /* bottom: auto; */
    /* top: 0; */
    bottom: calc(100% - 40px);
  }
  .testChat{
    height: 100%;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: rgba(255, 255, 255, 0.5);
  }
  .testChatTop{
    position: absolute;
    top: 0;
    left: 0;
    height: 100px;
    width: 100%;
    background-color: blue;
  }
  .testChatInput{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
  }