.homePage{
    width: 100%;
    height: 100%;
}
.spacer{
    height: 2000px;
}

/* Banner */

.banner{
    height: 100%;    
    width: 100%;
    /* height: 200px; */
    overflow: hidden;
    text-align: center;   
    position: relative;
     /* Starting image for when the video is loading  */
    background-image: url("../../Media/bannerImageBeforeVideo.jpg");
    background-position: center;
    object-fit: cover;
    /* background-color: orange; */
}
.bannerVideo{
    object-position: 30%;
    width: 100% !important;
    height: 100% !important;
    object-fit: cover !important;
    display: inline-block;
}
.bannerText{
    width: 90%;
    position: absolute;
    bottom: 10px;   
    left: 5%;
    text-align: center;  
    color: white; 
    font-size: 30px;
    display: inline-block;
}
.bannerText2{
    padding: 10px;
    box-sizing: border-box;
    font-size: 20px;
}
.bannerButton{    
    padding: 15px 40px;    
    background-color: #D65E4D;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    transition: 500ms;
    margin: 36px;
    display: inline-block;    
    font-size: 20px;
}
.bannerButton:hover{
    background-color: rgb(37, 10, 100);
    box-shadow: 0 0 20px #f3a126b0;
}
.goldLetters{
    color: #FFE170;
    /* background-color: rgba(248, 217, 103, 0.14); */
    box-shadow: 0 0 20px #fcdb661c;
    border-radius: 25%;
    padding: -10px 10px;
    background-image: radial-gradient(rgba(255, 217, 0, 0.31), rgba(255, 217, 0, 0.1), rgba(94, 90, 66, 0))
}

/* Text Sectoins */

.butterflySection{
    background-attachment: fixed;    
    color: white;
    padding: 20px 20px;
    box-sizing: border-box;
    position: relative;
    text-align: center;
}
.orangeButterflySection{
    background: url("../../Media/butterflyOrange.jpg");
    background-attachment: fixed;    
}
.purpleButterflySection{
    background: url("../../Media/butterflyPurple.jpg");
    background-attachment: fixed;    
    padding-top: 0px;
    background-size: contain;
    background-attachment: fixed;
}
.infoTitle{
    font-size: 35px;
}
.infoText{
    padding: 10px;
    box-sizing: border-box;
    font-size: 16px;
    text-align: justify;
}

/* Circle Info Area */

.noBackgroundSection{
    min-height: 200px;
    width: 100%;
    background-color: #F7F7F7;
}
.circleInfoArea{
    padding: 20px 0;
}
.circleInfoAreaDesktop{
    padding-top: 120px;
}
.circleInfoBox{
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    justify-content: center;
    min-height: 16em;
    position: relative;
    text-align: left;
    text-align: center;
}
.circleInfoBox img{
    height: 18em;
    width: 18em;
    border-radius: 50%;
}
.circleInfoTextBox{
    width: calc(100% - 40px);
    background-color: white;
    min-height: 30px;
    display: inline-block;
    position: relative;
    z-index: 2;
    margin-top: -4em;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.4);
    padding: 10px;
}
.circleInfoTitle{
    color: #D65E4D;
    font-size: 30px;
    margin: 0;
}
.circleInfoContent{
    color: black;

}

/* Quote Box */

.quoteBox{
    height: 250px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../../Media/ladyWithPicture.jpg");
    background-attachment: fixed;
    background-size: cover;
    text-align: center;
}   
.quoteBox img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.quoteOverlay{
    background-color: #39215b65;
    position: absolute;
    height: 250px;
    width: 100%;
}
.quote{
    position: absolute;
    font-size: 30px;
    font-style: italic;
    color: white;
    width: 60%;
}

/* Directory */

.directoryTitle{
    font-size: 30px;
}
.directoryDescription{
    text-align: justify;
    padding-top: 20px;
}
.contentBox{
    display: inline-block;
    width: calc(100% - 40px);
    margin: 20px;
    background-color: white;
    vertical-align: top;
    padding: 10px;
    padding-bottom: 50px;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;   
} 
.contentBoxTitle{
    color: #D65E4D;
    font-size: 26px;
    font-weight: bold;
}
.contentBoxText{
    color: black;
    font-size: 14px;
    text-align: justify;
    margin-bottom: 40px;
    padding: 0 10px;
}
.contentBoxButton{    
    padding: 10px 20px;    
    background-color: #D65E4D;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    transition: 500ms;
    display: inline-block;    
    font-size: 16px;
    position: absolute;
    bottom: 10px;
    width: 90%;
    left: 5%;
    box-sizing: border-box;
}

/* Bottom Bar */

.bottomBar{
    width: 100%;
    height: auto;
    min-height: 200px;
    position: relative;
    background-color: #351563;
}
.bottomBarColumn{
    width: 50%;
    padding: 10px;
    padding-top: 20px;
    box-sizing: border-box;
    display: inline-block;
    text-align: center;
    color: white;
}
.bottomLink{
    padding: 5px;
}

/* Topnav Placeholder (depreciated) */

.topNavPlaceholder{
    position: fixed;
    top: 0;
    height: 60px;
    left: 0;
    width: 100%;
    background-color: white;
    z-index: 3;
}
.topNavPlaceholderTop{
    top: 0;
    bottom: auto;
}
.topNavPlaceholderBottom{
    top: auto;
    bottom: 0;
}